
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import base64 from 'vux/src/tools/base64/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import AppHeader from './Header'
import AppFooter from './Footer'

import axios from 'axios'
import qs from 'qs'
import stringify from 'fast-json-stable-stringify'

import {loginApi,initApi,randomUserApi} from '../utils/apis'
import {getUserToken, setUserToken, clearUserToken, getPath} from '../utils/common'


export default {
directives: {
TransferDom
},
components: {
AppHeader,
AppFooter,
XInput,
XButton,
Group,
Cell,
Box,
Toast,
Loading,
CheckIcon
},
data () {
return {
loginDisabled:false,
showToast:false,
toastType:'',
toastText:'',
showLoading:false,
loadingText:'',
confirmShow:false,
confirmText:'',
agree:true,
xm:'',
xh:'',
zkzh: '',
}
},
mounted:function(){
},
methods:{
loginButton(){
let _this = this;

if(_this.agree === false){
_this.toastType = "text";
_this.toastText = "请先阅读并勾选新生网上选宿舍相关说明哦~";
_this.showToast = true;
return false;
}
let formdata = {
'XH':_this.xh,
'ZKZH':_this.zkzh,
};
let postval = "key="+base64.encode(encodeURI(stringify(formdata)));
axios.post(loginApi, postval)
.then(function(res){
if(res.data.code === 0){
_this.toastType = "cancel";
_this.toastText = res.data.data;
_this.showToast = true;
}
if(res.data.code === 1){
setUserToken(res.data.data[1]);
if(getUserToken() && res.data.data[0]){
let nextPath = getPath(res.data.data[0]);
_this.$router.push({path: nextPath});
}
}
})
.catch(function(err){
console.log(err);
});
},
testButton(){
let _this = this;

axios.get(randomUserApi)
.then(function(res){
if(res.data.code === 1){
_this.xh = res.data.data.XH;
_this.zkzh = res.data.data.ZKZH;

_this.loadingText = "获得系统随机派发测试账号";
_this.showLoading = true;
setTimeout(function (){
_this.showLoading = false;
},2000);
}
})
.catch(function(err){
console.log(err);
});
},
buidingMaps(){
this.$router.push({path:'/Map'});
}
}
}



