
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Divider from 'vux/src/components/divider/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import base64 from 'vux/src/tools/base64/index.js'

import Uploader from 'vux-uploader'
import AppHeader from './Header'
import AppFooter from './Footer'

import axios from 'axios'
import qs from 'qs'
import stringify from 'fast-json-stable-stringify'

import {initApi,setinfoApi,qiniuTokenApi,imageCDN } from '../utils/apis'
import {getUserToken, setUserToken, getPath} from '../utils/common'

export default {
directives: {
TransferDom
},
components: {
AppHeader,
AppFooter,
XInput,
Popup,
XButton,
Group,
Cell,
Box,
XAddress,
Uploader,
Divider,
Checker,
CheckerItem,
XNumber,
Selector,
Checklist,
Step,
StepItem,
Toast
},
data () {
return {
step: 0,
XM:'',
XB:'',
YXMC:'',
RXQHK: '',
mobile:'',
JTRKS: 3,
JTZYCY: '家庭成员',
YZBM: '',
BRDH:'',
SZDQ:[],
SZDQ_CN: '',
XXDZ:'',
JTSR:0,
GXoption:['父亲','母亲','哥哥','姐姐','弟弟','妹妹','其他'],
infoArr: [],

addressData: ChinaAddressV4Data,
addressArea: '',
images:[],
uploadUrl:'//upload-z2.qiniup.com',
uploadToken:'',
loginDisabled: false,
jjdc:[],
JTJJtitle1:'1.父亲职业',
JTJJoption1:[
{key:'1',value:'公司股东高管、私营业主、公务员、事业单位或国企工作人员'},
{key:'2',value:'个体经营户'},
{key:'3',value:'进城务工人员或合同制工作人员'},
{key:'4',value:'务农或临时务工'},
{key:'5',value:'因身体或其他原因无法就业、失踪（联）或去世'},
],
JTJJtitle2:'2.母亲职业',
JTJJoption2:[
{key:'1',value:'公司股东高管、私营业主、公务员、事业单位或国企工作人员'},
{key:'2',value:'个体经营户'},
{key:'3',value:'进城务工人员或合同制工作人员'},
{key:'4',value:'务农或临时务工'},
{key:'5',value:'因身体或其他原因无法就业、失踪（联）或去世'},
],
JTJJtitle3:'3.父亲劳动能力',
JTJJoption3:[
{key:'1',value:'身体健康且有一技之长'},
{key:'2',value:'身体健康但无一技之长'},
{key:'3',value:'3-4级伤残或有一定劳动能力'},
{key:'4',value:'1-2级伤残'},
{key:'5',value:'完全丧失劳动能力、失踪（联）或去世'},
],
JTJJtitle4:'4.母亲劳动能力',
JTJJoption4:[
{key:'1',value:'身体健康且有一技之长'},
{key:'2',value:'身体健康但无一技之长'},
{key:'3',value:'3-4级伤残或有一定劳动能力'},
{key:'4',value:'1-2级伤残'},
{key:'5',value:'完全丧失劳动能力、失踪（联）或去世'},
],
JTJJtitle5:'5.医疗支出',
JTJJoption5:[
{key:'1',value:'家庭成员医疗费用个人负担部分在2000元及以下'},
{key:'2',value:'家庭成员医疗费用个人负担部分在2000-1万（含1万）'},
{key:'3',value:'家庭成员医疗费用个人负担部分在1-3万（含3万）'},
{key:'4',value:'家庭成员医疗费用个人负担部分在3万以上或患重大疾病'},
],
JTJJtitle6:'6.受灾情况',
JTJJoption6:[
{key:'1',value:'近两年内未遭受自然灾害'},
{key:'2',value:'近两年内遭受一般自然灾害，且影响家庭收入'},
{key:'3',value:'近两年内遭受较重自然灾害，影响家庭收入且造成财产损失'},
{key:'4',value:'近两年内遭受严重自然灾害，造成人身伤害和财产重大损失'},
],
JTJJtitle7:'7.家庭变故',
JTJJoption7:[
{key:'1',value:'近两年内未出现家庭变故'},
{key:'2',value:'近两年内家庭成员出现伤残、失踪或意外事故造成财产损失等情况'},
{key:'3',value:'近两年内家庭成员出现重大伤残、意外死亡或重大变故'},
],
JTJJtitle8:'8.政策优抚（多选）',
JTJJoption8:[
{key:'1',value:'独生子女'},
{key:'2',value:'少数民族'},
{key:'3',value:'除烈士子女外的优抚家庭子女、重点工程移民子女、见义勇为人员子女'},
{key:'4',value:'烈士子女、城乡低保户家庭子女、被县级以上民政部门认定为低收入家庭子女、建档立卡贫困户子女'},
],
showInfo: false,
toastText:'',
toastShow:false,
toastText2:'',
toastShow2:false,
confirmDisabled:false,
confirmShowLoading:false,
}
},
mounted:function(){
let _this = this;
let userToken = getUserToken();



if(userToken){


axios.get(qiniuTokenApi + '?token=' + userToken)
.then(function(res){
if(res.data.code === 0){
}
if(res.data.code === 1){
_this.uploadToken = res.data.data;
}
})
.catch(function(err){
console.log(err);
});


for(let i=0;i<this.JTRKS - 1;i++){
let obj = {index:i,name:'',age:'',relation:'',unit:'',job:'',income:'',health:'',mobile:''};
this.infoArr.push(obj);
}


axios.get(initApi + '?token='+userToken)
.then(function(res){
if(res.data.code === 0){
}
if(res.data.code === 1){
if(res.data.data.steps !== 'setinfo'){
let nowPath = getPath(res.data.data.steps);
_this.$router.push({path:nowPath});
}
_this.XM = res.data.data.info.XM;
_this.XB = res.data.data.info.XB;
_this.YXMC = res.data.data.info.YXMC;
}
})
.catch(function(err){
console.log(err);
});
}else{
_this.$router.push({path: '/'});
}
},
methods:{
change(val){

this.infoArr = [];


for(let i=0;i<this.JTRKS - 1;i++){
let obj = {index:i,name:'',age:'',relation:'',unit:'',job:'',income:'',health:'',mobile:''};
this.infoArr.push(obj);
}
},
submitButton(){

this.SZDQ_CN = value2name(this.SZDQ, ChinaAddressV4Data);
this.showInfo = true;
},
confirmButton(){
let _this = this;

_this.confirmDisabled = true;
_this.confirmShowLoading = true;

let formdata = {
'BRDH': _this.BRDH,//本人电话
'RXQHK':_this.RXQHK,//入学前户口
'JTRKS':_this.JTRKS,//家庭人口数
'YZBM':_this.YZBM,//邮政编码
'SZDQ':_this.SZDQ_CN ,//所在地区
'XXDZ':_this.XXDZ,//详细地址
'ZSR':_this.JTSR,//家庭收入
'JTRK':_this.infoArr,//家庭人口数组
'JJDC':_this.jjdc,//经济调查数据
'ZP':_this.images,//照片
};

let postval = "key="+base64.encode(encodeURI(stringify(formdata)));


axios.post(setinfoApi + '?token='+getUserToken(), postval)
.then(function(res){

if(res.data.code === 0){
_this.toastText2 = res.data.msg;
_this.toastShow2 = true;
}
if(res.data.code === 1){

_this.$router.push({path: '/selectdormitory'});
}
})
.catch(function(err){
console.log(err);
});
},

changeArea(val){
},

previewMethod(){},
addImageMethod(){
},
removeImageMethod(){},
uploadImage(){
let _this = this;
let file = _this.$refs.upload;


let config = {
headers:{'Content-Type':'multipart/form-data'}
};
let formdata = new FormData();
formdata.append("token",_this.uploadToken);
formdata.append("file",file.$refs.input.files[0]);

axios.post(_this.uploadUrl,formdata,config)
.then(function(res){
if(res.data.key){
let imgObj = {url:imageCDN + res.data.key};
_this.images.push(imgObj);
}
})
.catch(function(err){
console.log(err);
})
},
}
}



