
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import AppHeader from './Header'
import AppFooter from './Footer'

import axios from 'axios'
import qs from 'qs'
import stringify from 'fast-json-stable-stringify'

import {finishApi,initApi} from '../utils/apis'
import {getUserToken, setUserToken, clearUserToken, getPath} from '../utils/common'

export default {
directives: {
TransferDom
},
components: {
AppHeader,
AppFooter,
Divider,
Card,
XTable,
XButton,
Box,
XDialog,
Loading
},
data () {
return {
headerTitle:"",
showScrollBox: false,
item:[],
roommates:[],
loadingShow: false,
}
},
mounted:function(){
let _this = this;
_this.loadingShow = true;
let userToken = getUserToken();
if(userToken == null){
_this.$router.push({path: '/'});
}
axios.get(finishApi + '?token='+getUserToken())
.then(function(res){
if(res.data.code === 0){
_this.loadingShow = false;
}
if(res.data.code === 1){
_this.headerTitle = '<span>' + res.data.data.personal.XM + '</span> ( 学号:'
+ res.data.data.personal.XH + ' )';
_this.item.building_id = res.data.data.personal.LH;
_this.item.dormitory_id = res.data.data.personal.SSH;
_this.item.bed_id = res.data.data.personal.CH;
_this.item.price = res.data.data.personal.ZSF;

_this.roommates = res.data.data.roommate;
_this.loadingShow = false;
}
})
.catch(function(err){
console.log(err);
});
},
methods:{
howtofind () {
this.showScrollBox = true;
},
}
}



