
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


import axios from 'axios'
import qs from 'qs'
import stringify from 'fast-json-stable-stringify'

import {logoutApi} from '../utils/apis'
import {getUserToken, clearUserToken, getPath} from '../utils/common'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Cell,
Popup,
Group,
XButton,
Actionsheet,
TransferDom
},
data(){
return{
headTitle:'2018级新生在线选宿舍',
showMenus: false,
showKF:false,
menus: {
menu1:'刷新',
menu2:'怎样选宿舍？',
menu3:'联系客服',
menu4:'<span style="color:red">退出</span>'
},
}
},
methods:{
menuClick(key,item){
if(key === 'menu1'){
window.location.reload();
}
if(key === 'menu2'){
window.location.href = 'http://xgb.chd.edu.cn/';
}
if(key === 'menu3'){
this.showKF = true;
}
if(key === 'menu4'){
let _this = this;

axios.get(logoutApi +'?token='+getUserToken())
.then(function(res){
if(res.data.code === 1){
clearUserToken();
_this.$router.push({path: '/'});
}
})
.catch(function(err){
console.log(err);
});
}
}
},
}



