import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import SetInfo from '@/components/SetInfo'
import ShowInfo from '@/components/ShowInfo'
import SelectDormitory from '@/components/SelectDormitory'
import Confirm from '@/components/Confirm'
import Map from '@/components/Map'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Login
    },
    {
      path: '/SetInfo',
      name:'Set',
      component: SetInfo
    },{
      path:'/ShowInfo',
      name:'Show',
      component:ShowInfo
    },{
      path:'/SelectDormitory',
      name:'Select',
      component:SelectDormitory
    },{
      path:'/Confirm',
      name:'Conirm',
      component:Confirm
    },{
      path:'/Map',
      name:'Map',
      component:Map
    }
  ]
})



