let storage=window.localStorage;
if(!storage){
	console.error("浏览器不支持localStorage!");
}

export let getUserToken = function(){
	return storage.getItem('userToken');
}

export let setUserToken = function(token){
	storage.setItem('userToken',token); 
}

export let clearUserToken = function(){
	storage.removeItem('userToken');
}

export let getPath = function(step){
	let path = '';
	switch (step){
		case 'setinfo':
			path = 'setinfo';
			break;
		case 'select':
			path = 'selectdormitory';
			break;
		case 'waited':
			path = 'confirm';
			break;
		case 'finished':
			path = 'showinfo';
		// default:
		// 	path = 'setinfo';
		// 	break;
	}
	return path;
}


