
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import base64 from 'vux/src/tools/base64/index.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import AppHeader from './Header'
import AppFooter from './Footer'

import axios from 'axios'
import qs from 'qs'
import stringify from 'fast-json-stable-stringify'

import {initApi,confirmApi} from '../utils/apis'
import {getUserToken, setUserToken, getPath} from '../utils/common'

export default {
directives: {
TransferDom
},
components: {
AppHeader,
AppFooter,
Box,
XButton,
Confirm,
Step,
StepItem,
FormPreview,
Cell,
Countdown,
Clocker,
Divider
},
data () {
return {
step:2,
dormitoryStr: "",
list:[],
time: '2018-07-15 23:00:00',
show: true,
confirmShow: false,
cancelShow: false,
}
},
mounted:function(){
let _this = this;
let userToken = getUserToken();

if(userToken){


axios.get(initApi + '?token='+userToken)
.then(function(res){
if(res.data.code === 0){
}
if(res.data.code === 1){
let ssinfo = res.data.data.info;
if(res.data.data.steps !== 'waited'){
let nowPath = getPath(res.data.data.steps);
_this.$router.push({path:nowPath});
}

_this.time = ssinfo.end_time;
_this.dormitoryStr = ssinfo.LH+"#"+ssinfo.SSH+"-"+ssinfo.CH;
_this.list = [
{'label':'楼号','value':ssinfo.LH},
{'label':'宿舍号','value':ssinfo.SSH},
{'label':'床号','value':ssinfo.CH}
];
}
})
.catch(function(err){
console.log(err);
});
}else{
_this.$router.push({path: '/'});
}
},
methods:{
finish (index) {
this.show = false;
this.value = 'completed';
},
confirmSelect(){
this.confirmShow = true;
},
cancelSelect(){
this.cancelShow = true;
},
onConfirm(){

let formdata = {
'type': 'confirm'//确认的type为confirm
};
let _this = this;
let postval = "key="+base64.encode(encodeURI(stringify(formdata)));
axios.post(confirmApi + '?token='+getUserToken(),postval)
.then(function(res){
if(res.data.code === 0){
}
if(res.data.code === 1){

alert("选择成功");
_this.$router.push({path: '/showinfo'});
}
})
.catch(function(err){
console.log(err);
});
},
onCancel(){
let formdata = {
'type': 'cancel'//确认的type为confirm
};
let _this = this;
let postval = "key="+base64.encode(encodeURI(stringify(formdata)));
axios.post(confirmApi + '?token='+getUserToken(),postval)
.then(function(res){
if(res.data.code === 0){
alert(res.data.msg);
}
if(res.data.code === 1){

alert("已取消");
_this.$router.push({path: '/selectdormitory'});
}
})
.catch(function(err){
console.log(err);
});
}
}
}



